import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  jobs: []
}

const jobsSlice = createSlice({
    name: 'jobs',
    initialState,
    reducers: {
      setJobs(state, action){
        state.jobs = action.payload
      },
      asyncSetJobs(){}
    }
})

export const {setJobs, asyncSetJobs} = jobsSlice.actions
export default jobsSlice.reducer